import React, { Fragment } from 'react';
import { GOOGLE_MAPS_LINK } from '@/constants';
import { styWrapper } from './styles';

function LocationSection() {
  return (
    <Fragment>
      <div id="fh5co-couple-story" className="fh5co-section-gray" css={styWrapper}>
        <div className="container">
          <div className="row">
            <div className="col-md-8 col-md-offset-2 text-center fh5co-heading animate-box">
              <h2 className="main-font">Lokasi Acara</h2>
              <p className="sub-title">
                <a
                  href={GOOGLE_MAPS_LINK}
                  title="Click untuk melihat peta di Google Maps"
                  target="_blank"
                  rel="noreferrer"
                  style={{ color: '#828282' }}
                >
                  <strong>Kediaman mempelai wanita</strong>
                </a>{' '}
                <br />
                Jalan Tampomas Dalam IV No. 42, Kel. Petompon, Kec. Gajahmungkur, Kota Semarang, 50237
              </p>
              <a href={`https://goo.gl/maps/ZFcBnBuqu6Uikt6k9`}>
                <button className="btn btn-default btn-block">Buka GoogleMaps</button>
              </a>
            </div>
          </div>
          <div className="row">
            <div className="col-md-10 col-md-offset-1">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d3960.089072875652!2d110.4002466!3d-6.9987917!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xe52606a06a044506!2zNsKwNTknNTUuNyJTIDExMMKwMjQnMDguOCJF!5e0!3m2!1sid!2sid!4v1643402616247!5m2!1sid!2sid"
                width="100%"
                height="450"
                frameBorder="0"
                style={{ border: '0' }}
                allowFullScreen
                aria-hidden="false"
                tabIndex="0"
                title="Google Maps - Riko & Irna Wedding Party"
              ></iframe>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default React.memo(LocationSection);
